import { h, Component } from 'preact';
import { useEffect } from "preact/hooks";
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import Header from './header';

import mu from '../../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false, false);
const MARGIN_BOTTOM = mu.marginBottom();

function PageTitle(title) {
  useEffect(() => {
    document.title = `Homerun Race: ${title}`;
  }, [title]);
  return <h1>{title}</h1>;
}

const initialState = {
	isLoading: false,
	email: '',
	submenu: 'ads',
	jwtToken: util.getJwtToken(),
	newRaceArticle: {},
	newRaceUserImage: {},
};

@observer
class PublicRaceNews extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.inputRef = {};
		this.updateTimer = {};
    }

	async loadNews(props = this.props) {
		this.setState({ isLoading: true });
		const { newsStore, userStore } = props.stores;
		const { isAdmin } = userStore;
		const query = {
			tag: props.tag,
		};
		if (isAdmin) {
			query.showall = 1;
		}
		if (props.newsid) {
			query.id = props.newsid;
		}
		await newsStore.load({ query });
		if (props.newsid) {
			this.jumpToId(`news${props.newsid}`);
		}
		this.setState({ isLoading: false });
	}

	toggleShowNews = () => {
		const { showNews } = this.state;
		this.setState({
			showNews: !showNews,
		});
	}

    jumpTo = (e) => {
        e.preventDefault();
        const divId = e.target.parentElement.dataset.id || e.target.dataset.id;
        document.getElementById(divId).scrollIntoView({ behavior: 'smooth', block: 'start' });
    }

    jumpToId = (id) => {
        document.getElementById(id).scrollIntoView({ behavior: 'smooth', block: 'start' });
    }

	componentDidMount() {
		this.loadNews();
	}

	componentWillReceiveProps(nextProps) {
		if (nextProps.newsid !== this.props.newsid) {
			this.loadNews(nextProps);
		}
	}

	render() {
		const { appState, userStore, raceStore, newsStore } = this.props.stores;
		const { allUsers, isAdmin, user = {}, notifications = [], teams = [] } = userStore;
		const { col = 12, offset = 0, newsid } = this.props;
		const { language = 'en' } = appState;

        const { newsList } = newsStore;
        const artList = newsList;
		let news = artList && artList[0] ? artList[0] : null;
		if (newsid) {
			news = artList.find(e => e.id == newsid);
		}

		return (
			<>
				<Header stores={this.props.stores} />

				<div class='container-fluid position-relative' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>

                    {!news && <div class='d-flex justify-content-center my-5 py-5'>
                        <div class='spinner-border text-primary' role='status'>
                            <span class='sr-only'>Loading...</span>
                        </div>
                    </div>}

					{news && <div class='row' id={`news${news.id}`}>
						<div class={`col-${col} offset-${offset} col-sm-8 col-lg-8 offset-sm-2 offset-lg-2`}>
							{(news.images && news.images[0]) ? <img src={news.images[0].s3XXLargeLink} class='img-fluid border rounded-lg' style='min-width: 100%' /> : ''}
							<h1 class='display-4'>{isAdmin && <i class={`fas fa-eye${news.active ? ' text-success' : '-slash text-danger'} float-right`} />} {PageTitle(news.title)}</h1>

							<p class='lead'>
								<Markdown markdown={mu.replaceImages(news.ingress, news.images, language)} markdownOpts={mu.getMarkdownOptions()} />								
							</p>
							<div>
								<Markdown markdown={mu.replaceImages(news.body, news.images, language)} markdownOpts={mu.getMarkdownOptions()} />								
							</div>
                        </div>

						<div class={`col-${col} offset-${offset} col-sm-8 col-lg-8 offset-sm-2 offset-lg-2 mt-5 pt-5 border-top`}>
                            {artList && artList.map(art => <div class='row' id={`news-${art.id}`}>
                                <div class='col-12'>
                                    <div>
										{isAdmin && <i class={`fas fa-eye${art.active ? ' text-success' : '-slash text-danger'} float-right`} />}
										<a href={`/race/news/${art.id}`}>
											{mu.replaceImages(art.title, art.images, language, false)}
										</a>
									</div>
                                </div>
                            </div>)}

    					</div>
					</div>}

				</div>
			</>
		);
	}
}

export default PublicRaceNews;
